<template>
<!--    <el-card class="box-card">-->
<!--        <div slot="header" class="clearfix">-->
<!--            修改合同-->
<!--        </div>-->

        <div>
            <el-form ref="addcForm" :model="addcForm" :rules="rules" label-position="left" label-width="100px">
                <el-form-item label="客户名称:" prop="name">
                    <el-input v-model="addcForm.name"></el-input>
                </el-form-item>

                <el-form-item label="电话:" prop="userPhone">
                    <el-input v-model="addcForm.userName"></el-input>
                </el-form-item>

                <el-form-item label="地址" prop="addr">
                    <el-input v-model="addcForm.addr" ></el-input>
                </el-form-item>

                 <el-form-item label="传真" prop="fax">
                    <el-input  v-model="addcForm.fax" ></el-input>
                </el-form-item>

                 <el-form-item label="邮箱" prop="email">
                    <el-input v-model="addcForm.email" ></el-input>
                </el-form-item>

                 <el-form-item label="银行名称" prop="bankname">
                    <el-input v-model="addcForm.bankname" ></el-input>
                </el-form-item>

                 <el-form-item label="银行账号" prop="bankaccount">
                    <el-input v-model="addcForm.bankaccount" ></el-input>
                </el-form-item>

                 <el-form-item label="* 备注" prop="mark">
                    <el-input type="textarea" :autosize="{minRows:8, maxRows:20}" v-model="addcForm.mark" ></el-input>
                </el-form-item>

                <el-form-item>
                    <el-row>
                        <el-col :span="15">
                            <el-button type="primary" @click="postContract">确定</el-button>
                        </el-col>

                        <el-col :span="9">
                            <el-button type="danger" @click="resetContract">重置</el-button>
                        </el-col>
                    </el-row>
                </el-form-item>

            </el-form>
        </div>
<!--    </el-card>-->
</template>

<script>
    export default {
        name: "addcustomer",
        data() {
            return {
                addcForm: {
                    name: '',
                    userPhone: '',
                    addr: '',
                    fax: '',
                    email: '',
                    bankname: '',
                    bankaccount: '',
                    mark: ''
                },
                nullContract: {
                    name: '',
                    userPhone: '',
                    addr: '',
                    fax: '',
                    email: '',
                    bankname: '',
                    bankaccount: '',
                    mark: ''
                },
                rules: {
                    name: [
                        {required: true, message: '请输入合同名称', trigger: 'blur'}
                    ],
                    userPhone: [
                        {required: true, message: '请输入客户姓名', trigger: 'blur'}
                    ],
                    addr: [
                        {required: true, message: '请输入客户住址', trigger: 'blur'}
                    ],
                    fax: [
                        {required: true, message: '请输入客户传真', trigger: 'blur'}
                    ],
                    email: [
                        {required: true, message: '请输入客户邮箱', trigger: 'blur'}
                    ],
                    bankname: [
                        {required: true, message: '请输入客户银行名称', trigger: 'blur'}
                    ],
                    bankaccount: [
                        {required: true, message: '请输入客户银行账户', trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            resetContract() {
                this.$confirm("此操作将清空当前信息，是否继续？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                }).then(() => {
                    this.addcForm = this.cleanJson(this.addcForm);
                    this.$notify({
                        title: '成功',
                        message: '清空成功！',
                        type: "success"
                    })
                })
            },
            postContract() {
                this.$refs["addcForm"].validate((valid) => {
                    if (valid) {
                        
                        this.$axios({
                            url: "/customer/add",
                            method: 'post',
                            data: {
                                name: this.addcForm.name,
                                userPhone: this.addcForm.userPhone,
                                addr: this.addcForm.addr,
                                fax: this.addcForm.fax,
                                email: this.addcForm.email,
                                bankname: this.addcForm.bankname,
                                bankaccount: this.addcForm.bankaccount,
                                mark: this.addcForm.mark
                            },
                            transformRequest: [function (data) {
                                let ret = '';
                                for (let it in data) {
                                    ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
                                }
                                return ret
                            }],
                            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
                        }).then(res => {
                            // eslint-disable-next-line no-console
                            console.log(res);
                        })
                    } else {
                        this.$notify({
                            title: '失败',
                            message: '提交失败！',
                            type: "error"
                        })
                    }
                })
            },
            cleanJson(json) {
                for (let addcFormKey in json) {
                    json[addcFormKey] = ''
                }
                return json;
            }
        }
    }
</script>

<style scoped>
    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }
    /*.box-card {*/
    /*    width: 50%;*/
